html[data-core-lobby="true"] {
  font-size: 16px;
}
[data-core-lobby="true"] img {
  vertical-align: middle;
  max-width: 100%;
}
[data-core-lobby="true"] .c-main {
  flex: 1 0 auto;
}
[data-core-lobby="true"] .c-page__content {
  padding-bottom: 6rem;
  padding-top: 0;
  display: flex;
  flex-wrap: wrap;
}
[data-core-lobby="true"] .c-page__content [class*="c-product"] {
  flex: 0 0 auto;
  width: 100%;
  padding: 0 0.75rem;
  display: flex;
  align-items: flex-start;
}
[data-core-lobby="true"] .c-page__content [class*="c-product"][data-selected="false"] {
  display: none;
}
[data-core-lobby="true"] .c-page__content [class*="c-product"][data-queue="before"] {
  transform: translate3d(-110%, 0, 0);
  opacity: 0;
}
[data-core-lobby="true"] .c-page__content [class*="c-product"][data-queue="before"][data-selected="true"] {
  animation: 0.3s QueueBefore forwards;
}
[data-core-lobby="true"] .c-page__content [class*="c-product"][data-queue="after"] {
  transform: translate3d(110%, 0, 0);
  opacity: 0;
}
[data-core-lobby="true"] .c-page__content [class*="c-product"][data-queue="after"][data-selected="true"] {
  animation: 0.3s QueueAfter forwards;
}
@keyframes QueueBefore {
  0% {
    transform: translate3d(-110%, 0, 0);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes QueueAfter {
  0% {
    transform: translate3d(110%, 0, 0);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
[data-core-lobby="true"] #overlayComponents .c-toast-layer {
  z-index: 13;
}
[data-core-lobby="true"] .c-footer {
  z-index: 14;
}
@keyframes layer_open {
  0% {
    transform: translate3d(100%, 0, 0);
    visibility: hidden;
    opacity: 0;
  }
  99% {
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes open_fade-in {
  0% {
    transform: translate3d(100%, 0, 0);
    visibility: hidden;
    opacity: 0;
  }
  99% {
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes open_fade-out {
  0% {
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
  }
  99% {
    transform: translate3d(100%, 0, 0);
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: hidden;
    opacity: 0;
    display: none !important;
  }
}
@keyframes fadein_up {
  0% {
    transform: translate3d(0, 10%, 0);
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
  }
  99% {
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
    overflow: hidden;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadein_down {
  0% {
    transform: translate3d(0, -10%, 0);
    visibility: hidden;
    opacity: 0;
  }
  99% {
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes Scale_In {
  0% {
    transform: scale(0.95);
    visibility: hidden;
    opacity: 0;
  }
  99% {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes Fade_In {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  99% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeout_down {
  0% {
    transform: translate3d(0, 0, 0);
    visibility: visible;
    opacity: 1;
  }
  99% {
    transform: translate3d(0, -100%, 0);
    visibility: hidden;
    opacity: 0;
  }
  100% {
    visibility: hidden;
    opacity: 0;
    display: none !important;
  }
}
[data-core-lobby="true"] [class*="c-iconcolor"] {
  flex: 0 0 auto;
  display: inline-flex;
  align-self: center;
}
[data-core-lobby="true"] [class*="c-iconcolor"]::before {
  content: "";
  flex: 0 0 auto;
  display: inline-flex;
  width: 48px;
  height: 48px;
  background-image: url("https://mbi.vixcdn.com/Content/SiteSkin/_global/common/images/UI_Core/iconcolor.png?v=20210127");
  transform-origin: 0 0;
}
[data-core-lobby="true"] .c-iconcolor-bonus-gold::before {
