[data-core-lobby="true"] .c-checkbox input:checked ~ .c-icon::before,
[data-core-lobby="true"] .c-radio input:checked ~ .c-icon::before,
.c-toast .c-checkbox [data-core-lobby="true"] input:checked ~ .c-icon::before,
.c-toast .c-radio [data-core-lobby="true"] input:checked ~ .c-icon::before {
  -webkit-mask-image: url("data:image/svg+xml;base64,");
