@keyframes badgeAdding {
  0% {
    transform: scale(1);
    animation-timing-function: ease-out;
    opacity: 1;
  }

  10% {
    transform: scale(3);
    animation-timing-function: ease-in;
    opacity: 0.6;
  }

  38% {
    transform: scale(1);
    animation-timing-function: ease-out;
    opacity: 1;
  }

  55% {
    transform: scale(1.5);
    animation-timing-function: ease-in;
  }

  72%,
  89%,
  to {
    transform: scale(1);
    animation-timing-function: ease-out;
  }

  81% {
    transform: scale(1.24);
    animation-timing-function: ease-in;
  }

  96% {
