.tooltips {
  z-index: 3;
  display: none;
  pointer-events: none;
  position: absolute;
  margin-top: 5px;
  left: 0;
  right: 0;
}

.tooltips-top {
  transform: translate(0, -100%);
}

.tooltips-top .tooltips_arrow {
  position: absolute;
  border-color: transparent;
  border-style: solid;
  bottom: -5px;
  left: 50%;
  margin-left: -5px;
  border-width: 8px 8px 0;
