.live-tip {
  max-height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  pointer-events: none;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 100%);
  transition: opacity 200ms ease-in;
}

.live-tip[data-status="is-open"] {
  pointer-events: auto;
  opacity: 1;
}

.live-tip[data-status="is-open"] .live-tip__item {
  transform: translateY(0);
  transition: all 200ms ease-in;
}

.live-tip[data-status="is-open"] .live-tip__item.pre-remove {
  opacity: 0;
  transition: all 200ms ease-in;
  transform: translateX(-120%);
}

.live-tip[data-expend="off"] {
  max-height: 100px;
}

.live-tip[data-expend="off"] .live-tip__item {
  position: absolute;
  top: -80px;
  left: 10px;
  right: 10px;
  transition: all 200ms ease-in;
}

.live-tip[data-expend="off"] .live-tip__item:nth-last-child(1) {
  top: 10px;
  transform: scale(1);
  z-index: 10;
}

.live-tip[data-expend="off"] .live-tip__item:nth-last-child(2) {
  top: 10px;
  transform: scale(0.95);
  z-index: 9;
  margin-top: 8px;
}

.live-tip[data-expend="off"] .live-tip__item:nth-last-child(3) {
  top: 10px;
  transform: scale(0.9);
  z-index: 8;
  margin-top: 16px;
}

.live-tip[data-expend="off"] .live-tip__item:nth-last-child(4),
.live-tip[data-expend="off"] .live-tip__item:nth-last-child(5),
.live-tip[data-expend="off"] .live-tip__item:nth-last-child(6),
.live-tip[data-expend="off"] .live-tip__item:nth-last-child(7),
.live-tip[data-expend="off"] .live-tip__item:nth-last-child(8),
.live-tip[data-expend="off"] .live-tip__item:nth-last-child(9),
.live-tip[data-expend="off"] .live-tip__item:nth-last-child(10) {
  top: 10px;
  transform: scale(0);
  z-index: 7;
  margin-top: 38px;
}

.live-tip[data-expend="off"] .live-tip__head {
  display: none;
}

.live-tip[data-expend="on"] {
  bottom: 0;
  background-image: none;
  background-color: rgba(0, 0, 0, 0.54);
  -webkit-backdrop-filter: blur(1px);
  backdrop-filter: blur(1px);
}

.live-tip[data-expend="on"] .live-tip__body .btn-close {
  display: none;
}

.live-tip[data-expend="on"] .live-tip__content {
  background-color: #fff;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item {
  opacity: 0;
  transition: all 200ms ease-in;
  transform: translateX(-120%);
  transition-delay: 1s;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(1) {
  transition-delay: 100ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(2) {
  transition-delay: 200ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(3) {
  transition-delay: 300ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(4) {
  transition-delay: 400ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(5) {
  transition-delay: 500ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(6) {
  transition-delay: 600ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(7) {
  transition-delay: 700ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(8) {
  transition-delay: 800ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(9) {
  transition-delay: 900ms;
}

.live-tip[data-expend="on"][data-remove="true"] .live-tip__item:nth-child(10) {
  transition-delay: 1000ms;
}

.live-tip__head {
  font-size: 20px;
  padding-left: 8px;
  margin-bottom: -8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

.live-tip__body {
  padding: 8px;
  display: flex;
  flex-direction: column-reverse;
}

.live-tip__item {
  margin-bottom: 8px;
  position: relative;
  z-index: 7;
  transform: translateY(-100px);
  transition: all 200ms ease-in;
  border-radius: 4px;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.25);
  animation: livetipItemEnter 200ms ease-in;
  overflow: hidden;
}

@keyframes livetipItemEnter {
  from {
    transform: translateY(-100px);
    max-height: 0;
  }

  to {
    transform: translateY(0);
    max-height: 100px;
  }
}

.live-tip__item .btn-close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.live-tip__item .btn-close .icon {
  color: var(--livetip-title-text);
}

.live-tip__content {
  padding: 8px;
  border-radius: 4px;
  background-color: var(--livetip-bg);
  -webkit-backdrop-filter: blur(11px);
  backdrop-filter: blur(11px);
}

.live-tip__header {
  display: flex;
  margin-bottom: 8px;
  font-size: 12px;
  padding-right: 24px;
}

.live-tip__badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  margin-right: 8px;
  background-color: var(--promo-color);
  color: #fff;
  border-radius: 4px;
}

.live-tip__badge .icon {
  width: 12px;
  height: 12px;
  font-size: 12px;
  transform-origin: 50% 10%;
  animation: bellShake 700ms ease-in-out infinite;
  animation-iteration-count: 5;
}

@keyframes bellShake {
  0%,
  90%,
  100% {
    transform: translateX(0);
  }

  10%,
  50% {
    transform: rotate(-7deg);
  }

  30%,
  70% {
    transform: rotate(7deg);
  }
}

.live-tip__title {
  flex: 1;
  color: var(--livetip-title-text);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.live-tip__detail {
  font-size: 13px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.tutorial-live-tip {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  pointer-events: none;
  transition: opacity 300ms ease-in;
