.parlayHelp {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 14;
  font-size: 14px;
  line-height: 1.4em;
  color: rgba(0, 0, 0, 0.87);
  background-color: #fff;
  transform: scale(0);
  transform-origin: 10% 0%;
  transition: all 200ms ease-in;
  opacity: 0;
}

[data-darkmode="true"] .parlayHelp {
  background-color: var(--body-color);
}

.parlayHelp::before,
.parlayHelp::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.parlayHelp::before {
  z-index: 1;
  background-color: #5f86c5;
  opacity: 0.2;
  -webkit-filter: hue-rotate(10deg) saturate(130%);
  filter: hue-rotate(10deg) saturate(130%);
}

.parlayHelp::after {
  z-index: 2;
  /* background-image: url(../public/Common/parlayHelp_logo-pattern.png?.45989); */
  background-repeat: no-repeat;
  background-size: cover;
}

[data-darkmode="true"] .parlayHelp::after {
  -webkit-filter: invert(1);
  filter: invert(1);
}

.parlayHelp > div:not(.parlayHelp__close):not(.parlayHelp__rect) {
  height: 100%;
}

.parlayHelp[data-status="is-open"] {
  transform: scale(1);
  opacity: 1;
  transition: all 200ms ease-in;
}

.parlayHelp__close {
  position: absolute;
  top: 32px;
  right: 18px;
  width: 30px;
  height: 30px;
  border-radius: 15px;
