:root {
  --network-text: rgba(0, 0, 0, 0.87);
  --network_panel-bg: #fff;
  --network_btn-text: rgba(0, 0, 0, 0.54);
  --network_btn-bg: rgba(0, 0, 0, 0);
  --network_btn-border: rgba(0, 0, 0, 0.38);
  --network_btn-active-text: rgba(0, 0, 0, 0.87);
  --network_btn-active-bg: #e2e2e2;
  --network_icon-default: #bbb;
  --network_header-color: #fff;
}

[data-darkmode="true"]:root {
  --network-text: rgba(255, 255, 255, 0.7);
  --network_panel-bg: #262626;
  --network_btn-text: rgba(255, 255, 255, 0.54);
  --network_btn-bg: #353535;
  --network_btn-border: #262626;
  --network_btn-active-text: #fff;
  --network_btn-active-bg: #000;
  --network_icon-default: rgba(255, 255, 255, 0.4);
  --network_header-color: #fff;
}

.network {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 20;
  display: none;
}

.network_panel {
  border-radius: 4px;
  padding-top: 0;
  width: 90vw;
  z-index: 12;
  overflow: hidden;
  -webkit-box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.19);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.19);
}

.network_header {
  padding: 8px 12px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  display: flex;
  align-items: center;
  background-color: var(--primary-color);
  color: var(--network_header-color);
}

.network_header .icon-clear,
.network_header .c-icon--clear {
  margin-left: auto;
}

.network_list {
  padding: 8px 12px;
  background-color: var(--network_panel-bg);
}

.network_title {
  padding: 8px 12px;
  background-color: var(--network_panel-bg);
  font-size: 14px;
  color: var(--network-text);
}

.network_btn {
  cursor: pointer;
  width: 100%;
  display: flex;
  justify-content: space-between;
  color: var(--network_btn-text);
  background-color: var(--network_btn-bg);
  border: 1px solid var(--network_btn-border);
  border-radius: 4px;
  padding: 8px;
  margin: 12px 0;
}

.network_btn:first-child {
  margin-top: 0;
}

.network_btn[data-status="is-active"] {
  color: var(--network_btn-active-text);
  background-color: var(--network_btn-active-bg);
}

.network .overlay {
  height: 100vh;
  pointer-events: none;
}

.network[data-status="is-open"] {
  display: flex;
}

.network[data-status="is-open"] .network_panel {
  animation: zoomIn 0.3s both;
}

.network[data-status="is-open"] .network_panel .network_btn {
  animation: fadeInRight 0.3s ease-out backwards;
}

.network[data-status="is-open"] .network_panel .network_btn:nth-child(1) {
  animation-delay: 0s;
}

.network[data-status="is-open"] .network_panel .network_btn:nth-child(2) {
  animation-delay: 0.1s;
}

.network[data-status="is-open"] .network_panel .network_btn:nth-child(3) {
  animation-delay: 0.12s;
}

.network[data-status="is-open"] .network_panel .network_btn:nth-child(4) {
  animation-delay: 0.14s;
}

.network[data-status="is-open"] .network_panel .network_btn:nth-child(5) {
  animation-delay: 0.16s;
}

.network[data-status="is-open"] .overlay {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.network[data-status="is-loading"] {
  display: flex;
}

.network[data-status="is-loading"] .network_panel {
  display: none;
}

.network[data-status="is-loading"] .msg {
  display: flex;
  animation: fadeIn 0.3s both;
}

.network[data-status="is-success"] {
  display: flex;
}

.network[data-status="is-success"] .network_panel {
  display: none;
}

.network[data-status="is-success"] .msg--success {
  display: flex;
  animation: fadeIn 0.3s both;
}

.network[data-status="is-warning"] {
  display: flex;
}

.network[data-status="is-warning"] .network_panel {
  display: none;
}

.network[data-status="is-warning"] .msg--warning {
  display: flex;
  animation: fadeIn 0.3s both;
}

.network_icon {
  display: flex;
}

.network_icon [class^="svg-network"] {
  fill: var(--network_icon-default);
}

.network_icon[data-network="full"] [class^="svg-network"] {
  fill: #41c300;
}

.network_icon[data-network="middle"] .svg-network_2,
.network_icon[data-network="middle"] .svg-network_3,
.network_icon[data-network="middle"] .svg-network_4 {
  fill: #41c300;
}

.network_icon[data-network="low"] .svg-network_3,
.network_icon[data-network="low"] .svg-network_4 {
  fill: #41c300;
}

.msg {
  padding: 12px;
  border-radius: 4px;
  width: 90%;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  z-index: 13;
  background-color: var(--msg-default-bg);
  color: var(--white-color1);
  font-size: 14px;
  justify-content: center;
  align-items: center;
  position: fixed;
  display: none;
  -webkit-box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.19);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.19);
}

.msg strong {
  font-size: 16px;
}

.msg p {
  margin-bottom: 0;
}

.msg .icon {
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 12px;
  border-radius: 50%;
  font-weight: bold;
  background-color: #ffffff;
}

.msg .icon.icon-switch {
  color: #7f7f7f;
}

.msg .icon.icon-done,
.msg .icon.c-icon--done,
.msg .side-switch-ver_btns .icon.btn[data-status="is-active"],
.side-switch-ver_btns .msg .icon.btn[data-status="is-active"],
.msg .page_content .switch-group .icon.btn[data-status="is-active"],
.page_content .switch-group .msg .icon.btn[data-status="is-active"] {
  color: #5cb379;
}

.msg .icon.icon-exclamation-word {
  color: #ec6363;
}

.msg--success {
  background: #5cb379;
}

.msg--warning {
  background: #ec6363;
}

.msg--noconnect,
.msg--reconnect {
  -webkit-box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.19);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.19);
  display: flex;
  padding: 12px;
  width: auto;
  border-radius: 4px;
  top: 4px;
  left: 4px;
  right: 4px;
  z-index: 13;
  background: var(--msg--noconnect-bg, var(--panel-color3));
  font-size: 14px;
  justify-content: center;
  align-items: center;
  position: fixed;
  transform: translate3d(0, calc(-100% - 20px), 0);
  transition: 0.5s;
}

.msg--noconnect[data-show="true"],
.msg--reconnect[data-show="true"] {
  transform: translate3d(0, 0, 0);
}

.msg--noconnect p,
.msg--reconnect p {
  margin-bottom: 0;
  margin-top: 4px;
}

.msg--noconnect > .icon,
.msg--reconnect > .icon {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: normal;
  margin-right: 12px;
  border-radius: 50%;
}

.msg--noconnect .msg__text,
.msg--reconnect .msg__text {
  flex: 1;
}

.msg--noconnect {
  color: var(--msg-noconnect-color, var(--dark-color1));
}

.msg--noconnect strong {
  color: var(--msg-noconnect-strong-color, #ec6363);
}

.msg--noconnect .icon {
  background-color: var(--msg-noconnect-icon-bg, #ec6363);
  color: var(--msg-noconnect-icon-color, #fff);
}

.msg--reconnect {
  color: var(--msg-reconnect-color, var(--dark-color1));
}

.msg--reconnect strong {
  color: var(--msg-reconnect-strong-color, #5cb379);
}

.msg--reconnect .icon {
  background-color: var(--msg-reconnect-icon-bg, #5cb379);
  color: var(--msg-reconnect-icon-color, #fff);
}

.ani-loading {
